.cd-header #cd-logo img {
  display: block; }

@media only screen and (min-width: 768px) {
  .cd-header {
    height: 70px; }
  .cd-header #cd-logo {
    margin: 23px 0 0 5%; } }

.cd-main-nav {
  float: right;
